@font-face { font-family: 'NanumBarunpen Bold'; src: url(./fonts/NanumBarunpenB.ttf) format('truetype'); }

.Home {
    
    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: column;
}


.Main {
    align-items: center;

    display: flex;
    flex-direction: column;
    
    height: 100%;
    width: 100%;
}

.imgBG {
    position: absolute;

    height: 100%;
    width: auto;
}

.StartButton {
    position: absolute;
    transform: translate(-50%, 50%);

    color: white;
    background: #2e425c;
    padding: 1vh 3vh;

    font-family: 'NanumBarunpen Bold';
    line-height: 1.7;

    border: 3px solid #95c8e6;
    border-radius: 47.7px;
    box-shadow: 0 0 11.7px 0 #008cff;
    outline: 0;

    margin-top: 63vh;

    font-size: 2vh;
}